*
  margin: 0
  padding: 0
  box-sizing: border-box

.container
  max-width: 800px
  width: 100%
  padding: 15px 20px
  margin: 0 auto
.top
  font-size: 0
  display: flex
  justify-content: space-between
  &__inp
    font-size: 20px
    width: 88%
  &__btn
    width: 10%
    font-size: 20px
    color: white
    text-decoration: none
    border-radius: 3px
    background: rgb(64,199,129)
    box-shadow: 0 -3px rgb(53,167,110) inset
    transition: 0.2s
    &::active
      background: rgb(33,147,90)
      box-shadow: 0 3px rgb(33,147,90) inset
    &:hover
      cursor: pointer

.bottom
  width: 100%

